let id = 0;
let callbacks = {};
window.JsBridgeCallBack = (args)=>{
    let callback = callbacks[args.id];
    if(callback){
        callback(args);
    }
}
let sendMessage = (fun,msg,format)=>{
    return new Promise((resolve, reject) => {
        let _id = id++;
        callbacks[_id] = v => {
            delete callbacks[_id]
            let _format = format(v);
            resolve(_format);
        };
        fun.postMessage(JSON.stringify({id:_id,msg}))
    });
}

export default {
    pickImage(msg){//msg in ['camera','gallery']选择单张图片
        return sendMessage(JsBridgePickImage,msg,(args)=>args.files[0])
    },
    pickImageMulti(msg){//msg is a number 选择多张图片
        return sendMessage(JsBridgePickImageMulti,msg,(args)=>args.files);
    },
    cropImage(msg){//msg is image path 切图
        return sendMessage(JsBridgeCropImage,msg,(args)=>args.files[0]);
    },
    openNewWeb(msg){//msg is url path 打开新web页面 没有返回值
        return sendMessage(JsBridgeOpenNewWeb,msg,(args)=>args);
    },
    back(){//返回上一个页面 没有返回值
        return sendMessage(JsBridgeBack,null,(args)=>args);
    },
    recordingAudio(){//录音
        return sendMessage(JsBridgeRecordingAudio,null,(args)=>args.files[0]);
    },
}